@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300&display=swap);
body {
	margin: 0;
	height: 100%;
}

html {
	height: 100vh;
	margin: 0 15vw 0 15vw;
	font-family: 'Kumbh Sans', sans-serif;
}

#root {
	height: 100%;
}

@media (min-width: 320px) and (max-width: 600px) {
	html {
		height: 100vh;
		margin: 0;
		font-family: 'Kumbh Sans', sans-serif;
	}
}

.navbar{display:flex;align-items:center;justify-items:center;grid-gap:50px;gap:50px;border-bottom:1px solid #d3d3d3;height:15vh}.navbar .burguer-items{display:none}.navbar .links-desktop{display:flex;text-decoration:none;height:100%;align-items:center}.navbar .links-desktop:hover{border-bottom:3px solid #ff7d1a}.navbar .nav-bar-links-burger{display:none}.navbar .nav-bar-links-burger .close-button{display:none}.navbar .cart{margin-left:auto;display:block}.navbar .profile-avatar{border-radius:50%;cursor:pointer}.navbar .profile-avatar:hover{outline:2px solid #ff7d1a}@media (min-width: 320px) and (max-width: 600px){.navbar{display:flex;align-items:center;justify-items:center;grid-gap:1rem;gap:1rem;border-bottom:none;height:8vh;margin:1rem}.navbar .navbar-burguer{display:flex;flex-direction:column;grid-gap:3px;gap:3px;cursor:pointer}.navbar .navbar-burguer div{background-color:#858585;height:3px;width:15px}.navbar .nav-bar-links{display:none}.navbar .links-desktop{display:none}.navbar .nav-bar-links-burger{display:none;flex-direction:column;position:fixed;background-color:#fff;top:0;left:0;height:100vh;transition:2s linear width}.navbar .nav-bar-links-burger .burguer-items{padding:2rem;display:block}.navbar .nav-bar-links-burger .burguer-items .close-button{width:20px;cursor:pointer;display:block}.navbar .nav-bar-links-burger .burguer-items .links{display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;margin-top:3rem;font-weight:1000;font-size:1.2rem}.navbar .active{display:flex;width:70vw}.navbar .cart{margin-left:auto;display:block}.navbar .profile-avatar{height:35px;border-radius:50%;cursor:pointer}.navbar .profile-avatar:hover{outline:2px solid #ff7d1a}}

.item{display:grid;grid-template-columns:auto auto auto;align-items:center;justify-items:center}.item-image{width:50px;height:50px;border-radius:5px}.item-delete{cursor:pointer}

.cart-num{position:relative;bottom:20px;left:40px;background:#ff7d1a;padding:0 10px 0 10px;text-align:center;color:#fff;align-self:center;border-radius:10rem;font-weight:1000}.cart-img{height:25px;cursor:pointer}.cart-detail{position:absolute;background:#fff;box-shadow:0px 0px 15px rgba(0,0,0,0.1);border-radius:0.5rem;width:400px;right:200px;top:120px}.cart-detail .cart__detail__title{padding:1.3rem;border-bottom:1px solid rgba(0,0,0,0.1)}.cart-detail .cart__detail__items{padding:0 1.5rem 1.5rem 1.5rem}.cart-detail .cart-checkout{cursor:pointer;width:100%;padding:1rem;border:none;background:#ff7d1a;color:#fff;font-weight:1000;border-radius:0.5rem}@media (min-width: 320px) and (max-width: 600px){.cart-num{position:relative;bottom:20px;left:40px;background:#ff7d1a;padding:0 10px 0 10px;text-align:center;color:#fff;align-self:center;border-radius:10rem;font-weight:1000}.cart-img{height:25px;cursor:pointer}.cart-detail{position:absolute;background:#fff;box-shadow:0px 0px 15px rgba(0,0,0,0.1);border-radius:0.5rem;width:95%;right:2%;top:14%}}

.empty-cart{display:grid;place-content:center;padding:1.5rem;color:#8f8f8f}

.thumbnails{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:2rem;gap:2rem}.thumbnails img{grid-column:auto;cursor:pointer;max-width:100%;max-height:100%;border-radius:1rem}.thumbnails img:hover{outline:3px solid orange;border-radius:1rem}.thumbnails .image-selected{outline:3px solid orange;border-radius:1rem}@media (min-width: 320px) and (max-width: 600px){.thumbnails{display:none}}

.image-viewer{display:flex;flex-direction:column;grid-gap:2rem;gap:2rem;width:100%;height:100%}.image-viewer .product-image img{max-width:100%;max-height:100%;border-radius:1rem;cursor:pointer}@media (min-width: 320px) and (max-width: 600px){.image-viewer{display:flex;flex-direction:column;grid-gap:2rem;gap:2rem;width:100%;height:100%}.image-viewer .product-image img{max-width:100%;max-height:100%;border-radius:0rem}}

.modal-container{position:absolute;background:rgba(0,0,0,0.65);height:100%;width:100%;top:0;left:0;display:grid;grid-template-columns:repeat(1, 75vh);place-content:center}.modal-close-button{margin-left:calc(100% - 5.2em);padding:1rem 0 1.5rem 0;cursor:pointer;-webkit-transform:scale(1.5);transform:scale(1.5)}.modal-close-button:hover path{fill:orange}.hidden{display:none}@media (min-width: 320px) and (max-width: 600px){.modal-container{display:none}}

.modal-image-viewer-container{display:grid;grid-template-columns:1fr auto 1fr;align-items:center}.modal-image-viewer-container .modal-next-picture-button,.modal-image-viewer-container .modal-prev-picture-button{cursor:pointer;position:relative;display:grid;background:white;border:none;height:7vh;width:7vh;border-radius:50%;margin-bottom:18vh;place-content:center}.modal-image-viewer-container .modal-next-picture-button:hover path,.modal-image-viewer-container .modal-prev-picture-button:hover path{stroke:orange}.modal-image-viewer-container .modal-next-picture-button{left:-2vw}.modal-image-viewer-container .modal-prev-picture-button{right:-2vw}

.counter-container{background:rgba(104,104,104,0.05);box-shadow:0px 0px 5px rgba(0,0,0,0.1);display:flex;align-items:center;border-radius:0.5rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.counter-container .minus,.counter-container .plus{font-size:2rem;border:none;cursor:pointer;padding:0 1rem 0 1rem;color:#ff7d1a;background:transparent}.counter-container span{font-weight:1000;padding:1rem;width:1rem;text-align:center}@media (min-width: 320px) and (max-width: 600px){.counter-container{background:rgba(104,104,104,0.05);box-shadow:0px 0px 5px rgba(0,0,0,0.1);display:flex;align-items:center;justify-content:space-between;border-radius:0.5rem;width:100%}}

.product-brand{color:#ff7d1a}@media (min-width: 320px) and (max-width: 600px){.product-brand{color:#ff7d1a;padding:0 1rem 0 1rem;font-size:1rem;font-weight:bolder;margin-top:0}.product-title{padding:0 1rem 0 1rem;font-size:2rem}.product-info{padding:0 1rem 0 1rem}.prices{display:flex;justify-content:space-between;padding:0 1rem 0 1rem}}

.title{color:#ff7d1a}.product-description{display:flex;flex-direction:column;justify-content:center;grid-gap:20px;gap:20px}.product-description h1{margin:0}.product-description .real-price{line-height:2rem;font-weight:1000;text-decoration:line-through;color:gray}.product-price{display:flex;align-items:center;grid-gap:20px;gap:20px}.product-price span{color:#ff7d1a;font-weight:1000;background:#ffede0;padding:5px;border-radius:5px}.description-buttons{display:flex;justify-content:space-between;grid-gap:20px;gap:20px}.description-buttons .addtocart-button{box-shadow:5px 2px 10px #ff7d1a;border:none;border-radius:0.5rem;width:100%;color:#fff;background:#ff7d1a;cursor:pointer;display:flex;align-items:center;justify-content:center}.description-buttons .addtocart-button .cart-img{-webkit-transform:scale(0.9);transform:scale(0.9);align-self:center}@media (min-width: 320px) and (max-width: 600px){.description-buttons{display:flex;flex-direction:column;justify-content:space-between;grid-gap:20px;gap:20px;padding:0 1rem 1rem 1rem}.description-buttons .addtocart-button{box-shadow:5px 2px 10px #ff7d1a;border:none;border-radius:0.5rem;width:100%;color:#fff;background:#ff7d1a;cursor:pointer;display:flex;align-items:center;justify-content:center;height:3.5rem}.description-buttons .addtocart-button .cart-img{-webkit-transform:scale(0.9);transform:scale(0.9);align-self:center}}

.product-page{display:grid;grid-gap:5vw;gap:5vw;grid-template-columns:repeat(2, 45%);padding:5vw;padding-bottom:0}@media (min-width: 320px) and (max-width: 600px){.product-page{display:grid;grid-gap:5vw;gap:5vw;grid-template-columns:auto;padding:0;padding-bottom:0}}

